.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-image: url('./assets/background.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.team-background{
  background: rgb(230,230,230);
background: -moz-linear-gradient(146deg, rgba(230,230,230,1) 0%, rgba(199,204,205,1) 100%);
background: -webkit-linear-gradient(146deg, rgba(230,230,230,1) 0%, rgba(199,204,205,1) 100%);
background: linear-gradient(146deg, rgba(230,230,230,1) 0%, rgba(199,204,205,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#e6e6e6",endColorstr="#c7cccd",GradientType=1);
transition: transform .5s ease;
}

.team-background:hover{
  background: rgb(12,105,71);
background: -moz-linear-gradient(0deg, rgba(12,105,71,1) 0%, rgba(13,156,103,1) 100%);
background: -webkit-linear-gradient(0deg, rgba(12,105,71,1) 0%, rgba(13,156,103,1) 100%);
background: linear-gradient(0deg, rgba(12,105,71,1) 0%, rgba(13,156,103,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#0c6947",endColorstr="#0d9c67",GradientType=1);
transform: scale(1.1);
}

.legal-text{
  line-height: 2;
}

#topinfo h1, #topinfo h4{ line-height: 1.5;}

.bg-back{
   background: rgb(12,105,71);
background: -moz-linear-gradient(0deg, rgba(12,105,71,1) 0%, rgba(13,156,103,1) 100%);
background: -webkit-linear-gradient(0deg, rgba(12,105,71,1) 0%, rgba(13,156,103,1) 100%);
background: linear-gradient(0deg, rgba(12,105,71,1) 0%, rgba(13,156,103,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#0c6947",endColorstr="#0d9c67",GradientType=1);
}

.zoom{ transition: transform .5s ease; }
.zoom:hover{ transform: scale(1.2); }

.social-top{
  max-width: 70px;
}